import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, ToastController } from '@ionic/angular';
import { AlertInternetComponent } from 'src/app/components/alert-internet/alert-internet.component';

@Injectable({
  providedIn: 'root'
})


export class ShowMessageService {
  modal: any = null;

  constructor(
    private toastController: ToastController,
    public modalController: ModalController,
    private router: Router) {
  }


  errorData: { code: any, message: string }[] = [
    { "code": 400, "message": "Bad Request" },
    { "code": 401, "message": "Unauthorized" },
    { "code": 403, "message": "Forbidden" },
    { "code": 404, "message": "Not Found" },
    { "code": 500, "message": "Internal Server Error" },
    { "code": 502, "message": "Bad Gateway" },
    { "code": 503, "message": "Service Unavailable" },
    { "code": 504, "message": "Gateway Timeout" },
    { "code": 0, "message": "Error" }
  ];

  errorMessage(status: any, errorResp?: any) {
    if (status === 0) {
      this.presentToast("Gateway Timeout!");
      // if (this.modal == null) {
      //   return;
      // } else {
      //   return;
      // }
    }
  }


  async openNoInternet() {
    this.modal = await this.modalController.create({
      component: AlertInternetComponent,
      cssClass: 'internet-modal'
    });
    await this.modal.present();

    const { role } = await this.modal.onDidDismiss();

    if (role == 'confirm') {
      this.router.navigate(['login-landing'], { replaceUrl: true });
      this.modal = null;
    }
  }


  async presentToast(msg: any, position?: any) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 800,
      position: position || 'bottom'
    });
    toast.present();
  }


}
