import { ChangeDetectorRef, Component } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

import {
  AlertController,
  MenuController,
  ModalController,
  Platform,
} from '@ionic/angular';
import { Config, isPlatform } from '@ionic/angular';

import { DeviceAccess } from 'src/app/services/device-access.service';
import { ApiService } from './services/api.service';
import { DataService } from './services/data.service';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { BackButtonService } from './services/back-button.service';

import { Network } from '@ionic-native/network/ngx';
import { fromEvent, merge, Observable, of } from 'rxjs';
import { mapTo } from 'rxjs/operators';
import { Router } from '@angular/router';
import { TokenStorageService } from './services/token-storage.service';
import { ROLES, Roles } from './services/user-roles';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  currentUser: any;
  avatar: any;
  isFaceId: any = false;
  isFingerPrint: boolean = false;
  i: number = 0;
  public appIsOnline$: Observable<boolean> = undefined;
  checkInterent: boolean;
  userRole: string;
  roles = Roles;
  customerId: string;

  constructor(
    private platform: Platform,
    private statusBar: StatusBar,
    private api: ApiService,
    private config: Config,
    private device: DeviceAccess,
    public dataService: DataService,
    private modalCtrl: ModalController,
    private router: Router,
    private alert: AlertController,
    private sanitizer: DomSanitizer,
    public menu: MenuController,
    private network: Network,
    private cdr: ChangeDetectorRef,
    private backButtonService: BackButtonService,
    private tokenService: TokenStorageService
  ) {
    //Check Permission on App Start
    this.device.getPhonePermission();

    this.initializeApp();

    this.platform.resume.subscribe(async () => {
      console.log('Resume event detected');
      localStorage.setItem('isShowed', 'yes');
    });

    this.platform.pause.subscribe(async () => {
      console.log('Pause event detected');
      localStorage.setItem('isShowed', 'no');
    });
  }

  ngOnInit() {
    this.currentUser = this.tokenService.getUser();
    this.userRole = this.currentUser?.roles[0]?.name;

    this.customerId = localStorage.getItem('customer_id');

    this.checkInternet().subscribe((isOnline) => {
      this.checkInterent = isOnline;
      console.log('interent', this.checkInterent);
      if (this.checkInterent == true) {
        console.log('Internet is working.');
      } else {
        console.log('Internet is slow or not working.');
        this.router.navigate(['internet'], { replaceUrl: true });
      }
      this.cdr.markForCheck();
    });

    if (this.platform.is('cordova')) {
      if (this.currentUser !== null) {
        this.getProfilePicture(this.customerId);
        if (this.currentUser?.faceLoginEnable) {
          this.router.navigate(['login-landing'], { replaceUrl: true });
          // direct faceid open
        } else if (this.currentUser?.fingerprintLoginEnable) {
          this.router.navigate(['login-landing'], { replaceUrl: true });
          // direct fingerprint tray open
        } else if (this.currentUser?.mpinActive) {
          // direct mpin login
          this.router.navigate(['login-landing'], { replaceUrl: true });
        } else {
          this.initUserContext();
          this.router.navigate(['login-landing'], { replaceUrl: true });
        }
      } else {
        this.router.navigate(['login'], { replaceUrl: true });
      }
    } else {
      if (this.currentUser !== null) {
        this.initUserContext();
        this.getProfilePicture(this.customerId);
        if (ROLES.includes(this.userRole)) {
          this.router.navigate(['dashboard'], { replaceUrl: true });
        } else {
          this.router.navigate(['no-permission'], { replaceUrl: true });
        }
      } else {
        this.router.navigate(['home'], { replaceUrl: true });
      }
    }

    this.dataService.getAvatarUrl.subscribe((data) => {
      if (data != null) {
        this.avatar = data;
        this.cdr.markForCheck();
      }
    });

    this.dataService.share.subscribe((data: any) => {
      if (data) {
        this.getCurrentUser();
        this.cdr.markForCheck();
      }
    });

    this.cdr.markForCheck();
  }

  checkInternet() {
    if (!window || !navigator || !('onLine' in navigator)) return;

    this.appIsOnline$ = Observable.create((observer) => {
      observer.next(true);
    }).pipe(mapTo(true));

    if (this.platform.is('cordova')) {
      // on Device - when platform is cordova
      this.appIsOnline$ = merge(
        this.network.onConnect().pipe(mapTo(true)),
        this.network.onDisconnect().pipe(mapTo(false))
      );
    } else {
      // on Browser - when platform is Browser
      this.appIsOnline$ = merge(
        of(navigator.onLine),
        fromEvent(window, 'online').pipe(mapTo(true)),
        fromEvent(window, 'offline').pipe(mapTo(false))
      );
    }

    return this.appIsOnline$;
  }

  initUserContext() {
    setInterval(async () => {
      this.getCurrentUser();
    }, 1000);
  }

  getCurrentUser() {
    this.currentUser = this.tokenService.getUser();
    this.userRole = this.currentUser?.roles[0]?.name;
    this.cdr.markForCheck();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.overlaysWebView(false);
      this.statusBar.backgroundColorByHexString('#3B59FD'); //
      this.backButtonService.init();
    });
  }

  public toggle() {
    this.menu.toggle('slidingMenu');
  }

  getProfilePicture(customerId) {
    const contentType = 'image/png';
    this.api.getProfileDetails(customerId).subscribe(
      (data: any) => {
        if (data?.profileImage != null) {
          let objectURL = data.profileImage;
          this.avatar =
            data.profileImage != 'not_available'
              ? this.sanitizer.bypassSecurityTrustUrl(objectURL)
              : undefined;
        } else {
          this.avatar = null;
        }
        this.cdr.markForCheck();
      },
      (error: any) => {
        this.avatar = null;
      }
    );
  }

  goToBranch() {
    this.menu.close();
    this.router.navigate(['branch-list']);
  }

  goToExchangerate() {
    this.menu.close();
    this.router.navigate(['exchangerate']);
  }

  goToTransaction() {
    this.menu.close();
    this.router.navigate(['transaction']);
  }

  goToAppointment() {
    this.menu.close();
    this.router.navigate(['appointment']);
  }

  goToFile() {
    this.menu.close();
    this.router.navigate(['file-upload']);
  }

  goToNotifications() {
    this.menu.close();
    this.router.navigate(['notification']);
  }

  goToChqD() {
    localStorage.removeItem('chequeData');
    this.menu.close();
    this.router.navigate(['cheque-deposit-v2']);
  }

  goToChqW() {
    localStorage.removeItem('chequeData');
    this.menu.close();
    this.router.navigate(['cheque-withdrawal-v2']);
  }

  async logoutApp() {
    let alret = await this.alert.create({
      header: 'Logout',
      subHeader: 'Are you sure want to Log out?',
      cssClass: 'logout-alert',
      buttons: [
        {
          text: 'Logout',
          cssClass: 'alert-button-confirm',
          handler: () => {
            this.goToFeedback();
            this.menu.close();
          },
        },
        {
          role: 'cancel',
          cssClass: 'alert-button-cancel',
          text: 'Cancel',
        },
      ],
    });
    await alret.present();
  }

  goToFeedback() {
    this.router.navigate(['feedback'], { replaceUrl: true });
  }
}
