  fetchAppointmentInfo(
    filterOption?: any,
    formattedFromDate?: any,
    formattedToDate?: any,
    customerId?: any,
    accountNumber?: any
  ) {
    var opt;
    if (
      (customerId != null || customerId != undefined || customerId != '') &&
      formattedFromDate == null &&
      formattedToDate == null &&
      filterOption == null
    ) {
      opt = new HttpParams().append('customerId', customerId);
    } else if (
      (formattedFromDate != null || formattedFromDate != '') &&
      (formattedToDate != null || formattedToDate != '') &&
      customerId != null &&
      filterOption == null
    ) {
      opt = new HttpParams()
        .append('customerId', customerId)
        .append('fromDate', formattedFromDate)
        .append('toDate', formattedToDate);
    } else if (
      customerId != null &&
      filterOption != null &&
      formattedFromDate == null &&
      formattedToDate == null
    ) {
      opt = new HttpParams()
        .append('customerId', customerId)
        .append('filterOption', filterOption);
    } else {
      opt = new HttpParams()
        .append('customerId', customerId)
        .append('fromDate', formattedFromDate)
        .append('toDate', formattedToDate)
        .append('filterOption', filterOption);
    }
    return this.http.get<any>(
      `${API_URL}/cash-deposit/api/fetchAppointmentInfo?${opt}`
    );
  }
  getTransactionByAccountId(
    accountId: any,
    page,
    formattedFromDate,
    formattedToDate,
    size
  ) {
    var params;
    if (
      (accountId != null || accountId != '' || accountId.length != 0) &&
      page == 0
    ) {
      console.log('only accountId');
      if (formattedFromDate != null && formattedToDate != null) {
        params = new HttpParams()
          .append('accountNumber', accountId)
          .append('fromDate', formattedFromDate)
          .append('toDate', formattedToDate);
      } else {
        if (size > 20) {
          params = new HttpParams()
            .append('accountNumber', accountId)
            .append('size', size);
        } else {
          params = new HttpParams().append('accountNumber', accountId);
        }
      }
    } else {
      console.log('accountId and page');

      if (formattedFromDate != null && formattedToDate != null) {
        params = new HttpParams()
          .append('accountNumber', accountId)
          .append('page', page)
          .append('fromDate', formattedFromDate)
          .append('toDate', formattedToDate);
      } else {
        if (size > 20) {
          params = new HttpParams()
            .append('accountNumber', accountId)
            .append('page', page)
            .append('size', size);
        } else {
          params = new HttpParams()
            .append('accountNumber', accountId)
            .append('page', page);
        }
      }
    }
    return this.http.get<any>(
      `${API_URL}/cash-deposit/api/fetchTransaction?${params}`
    );
  }

