import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { IonicModule, IonicRouteStrategy, isPlatform } from '@ionic/angular';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';

import { StatusBar } from '@ionic-native/status-bar/ngx';
import { BarcodeScanner } from '@ionic-native/barcode-scanner/ngx';
import { Camera } from '@ionic-native/camera/ngx';
import { CarouselModule } from 'ngx-owl-carousel-o';
import { Geolocation } from '@ionic-native/geolocation/ngx';
// MAPMYINDIA IMPORT
import { mmi } from 'mapmyindia-map-cordova-ionic-beta';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { SwiperModule } from 'swiper/angular';

import { AndroidPermissions } from '@ionic-native/android-permissions/ngx';
import { DirectiveModule } from './directives/directive.module';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio/ngx';
import { File } from '@ionic-native/file/ngx';
import { FilePath } from '@ionic-native/file-path/ngx';
import { Crop } from '@ionic-native/crop/ngx';
import { Network } from '@ionic-native/network/ngx';
import { UniqueDeviceID } from '@ionic-native/unique-device-id/ngx';
import { MatSelectCountryModule } from '@angular-material-extensions/select-country';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { Clipboard } from '@ionic-native/clipboard/ngx';
import { RouteReuseStrategy } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxDocumentScannerModule, OpenCVConfig } from 'ngx-document-scanner';

import { CameraPreview } from '@ionic-native/camera-preview/ngx';
import { InterceptorProviders } from './services/interceptors/interceptors';
import { AuthInterceptor } from './services/interceptors/auth.interceptor';
const openCVConfig: OpenCVConfig = {
  openCVDirPath: '/assets/opencv',
};

@NgModule({
  declarations: [AppComponent],
  entryComponents: [],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    CarouselModule,
    SwiperModule,
    IonicModule.forRoot({
      rippleEffect: true,
      mode: 'md',
    }),
    MatSelectCountryModule.forRoot('en'),
    AppRoutingModule,
    DirectiveModule,
    ReactiveFormsModule,
    FormsModule,
    NgxDocumentScannerModule.forRoot(openCVConfig),
  ],
  providers: [
    StatusBar,
    Camera,
    BarcodeScanner,
    Geolocation,
    SocialSharing,
    File,
    FilePath,
    Crop,
    Clipboard,
    AndroidPermissions,
    FingerprintAIO,
    mmi,
    Network,
    UniqueDeviceID,
    CameraPreview,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
